.beneficiary-error {
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}
.beneficiary-card-wrapper {
  display: flex;
  margin-top: 25px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}
.beneficiary-card-wrapper .beneficiary-card-title {
  display: block;
}
.beneficiary-card-wrapper .beneficiary-card {
  margin: 0 25px 25px 0;
  border: 1px solid #EAEAEA;
  box-shadow: 0 10px 50px 0 rgba(0, 116, 249, 0.1);
  border-radius: 5px;
}
.beneficiary-card-wrapper .beneficiary-card.editable {
  cursor: pointer;
}
.beneficiary-card-wrapper .beneficiary-card .beneficiary-card-proof {
  display: flex;
  padding: 5px 0;
  justify-content: center;
  align-items: center;
}
.beneficiary-card-wrapper .beneficiary-card .beneficiary-card-proof span {
  flex: 1 1;
  text-align: left;
}
.beneficiary-card-wrapper .beneficiary-card .beneficiary-card-proof svg {
  margin-right: 5px;
}
.beneficiary-card-wrapper .beneficiary-card.errors {
  border-color: #FF5F5F;
}
.beneficiary-card-wrapper .beneficiary-card .beneficiary-add {
  cursor: pointer;
  width: 80%;
  height: 80%;
  position: absolute;
  border: 2px dashed #0053FA;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 11px;
  color: #0053FA;
  letter-spacing: 0;
  text-transform: uppercase;
}
.beneficiary-card-wrapper .beneficiary-card .beneficiary-add svg {
  width: 32px;
  height: 32px;
  margin-bottom: 20px;
}
.beneficiary-card-wrapper .beneficiary-card .ant-card {
  width: 205px;
  min-height: 220px;
  height: 100%;
  border: 0px;
  box-shadow: 0 10px 50px 0 rgba(0, 116, 249, 0.1);
}

